	import selectAddress from '@/components/yixuan-selectAddress/yixuan-selectAddress.vue'
	
	import kpsImageCutter from "@/components/ksp-image-cutter/ksp-image-cutter.vue";
	import config from '../../components/config/config.vue'
    export default {
		components: {
			kpsImageCutter,
			selectAddress
		},
        data() {
            return {
				url: "",
				scrollHeight: 1280,
                form: {
                    image:'../../static/default.jpg',
                    title: '',
                    text:'',
					attribute: [],
					attribute2: []
                },
				
				winid:0,

                
            }
        },
        computed: {


        },
        onShow() {

        },
        onLoad:function (option)  {
			var systeminfo = uni.getSystemInfoSync();
            this.systeminfo = systeminfo;
            // console.log(systeminfo.windowHeight)
            this.scrollHeight = systeminfo.windowHeight - systeminfo.statusBarHeight - 46;
            // 目前在某些平台参数会被主动 decode，暂时这样处理。
			this.winid=option.id
			this.getolddata()
 
        },
        methods: {
			chooseImage() {
			    uni.chooseImage({
			        success: (res) => {
			            // 设置url的值，显示控件
			            this.url = res.tempFilePaths[0];
			        }
			    });
			},
			onok(ev) {
                this.form.image = ev.path;
				//上传
                this.url = "";
            },
            oncancle() {
                // url设置为空，隐藏控件
                this.url = "";
            },
            addTable(e, idx) {
                switch (e) {
                    case 'attribute':
					    if( this.form.attribute.length<5){
							 this.form.attribute.push({
							text:""
							})
						}else{
							config.txtshow("最大标签数为5")
						}                       
                        break;
                    default:
                        break;
                }
            },

            delTable(e, idx) {
                switch (e) {
                    case 'attribute':
                        this.form.attribute = this.form.attribute.filter((
                            item, i) => {								
                            if (i != idx) {

                                return e
                            }
                        })
                        break;
					case 'attribute2':
					    this.form.attribute2 = this.form.attribute2.filter((
					        item, i) => {								
					        if (i != idx) {					
					            return e
					        }
					    })
					    break;
                    default:
                        break;
                }
            },
			btnClick() {
			        this.$refs.selectAddress.show()
			    },
			successSelectAddress(address){ //选择成功回调
			    //console.log(address)
				this.form.attribute2.push({
				text:address
				})
			 },

            send() {
				var id=this.winid
                var form = this.form;
                var attribute = form.attribute; //标签
				var attribute2 = form.attribute2; //地址
                // 表单验证
                var err = {
                    msg: '',
                    ok: true
                }
				if(attribute.length<=0){
					err.ok = false;
					err.msg = "至少设置一个标签"
				}else{
					for (var i = 0; i < attribute.length; i++) {                   
                    if (!attribute[i].text) {
                        err.ok = false;
                        err.msg = "标签存在空值\n位置:" + (i + 1) 
                        break;
                    }
					}
				}
                
				if(attribute2.length<=0){
					err.ok = false;
					err.msg = "至少设置一个地址"
				}else{
					for (var i = 0; i < attribute2.length; i++) {
					if (!attribute2[i].text) {
					    err.ok = false;
					    err.msg = "地址存在空值\n位置:" + (i + 1) 
					    break;
					}
					}
				}
								
                if (form.title.length < 2 || form.title.length > 50) {
                    err.ok = false;
                    err.msg = "标题限制长度2-50";

                }
				if(form.text.length < 5 ||form.text.length > 1000 ){
                    err.ok=false;
                    err.msg="内容限制5-1000"
                }
                if (!err.ok) {
                    uni.showModal({
                        title: '提示',
                        content: err.msg
                    })
                    return;
                }
				var tmp=form.image
				if(tmp=="../../static/default.jpg"){
					tmp="../../static/default.png"
				}
				
				//标签
				var str=""
				for (var i = 0; i < attribute.length; i++) {
					str+=attribute[i].text+","

				}
				
				//地址
				var str2=""
				for (var i = 0; i < attribute2.length; i++) {
					str2+=attribute2[i].text+","				
				}
				
                uni.showLoading({
                    title: '数据处理中'
                });
				var value = uni.getStorageSync('token');
				uni.uploadFile({
					header: {
					'Authorization' : value,
					},
				    url: config.httpUrl+'/api/v1/updatecommodity', 
				    filePath:tmp,
					formData:{
						"id":id,
						"title":form.title,						
						"describe":form.text,
						"lable":str,
						"address":str2
					},
				    name: 'image',
				    success: function (uploadFileRes) {
				         if(JSON.parse(uploadFileRes.data)["code"]==0){
							  //返回
							  config.txtshow("修改成功")
							uni.redirectTo({
								url:'mypublished'
							})
							}else{
								config.txtshow(uploadFileRes.data)
							}
				        }
				     });                
            },
			//加载原信息
			getolddata(){
				var that=this
				var value = uni.getStorageSync('token');
				if(value){
				  	uni.request({
				  	    url: config.httpUrl +'/api/v1/commodity',
				  		method:"GET",
				  	    header: {
				  		   'Authorization' : value,
				  	    },
						data:{
							'id':this.winid, //0代表所有id
						},
				  	    success: (res) => {
				  			var jsondata=res.data
				  			if(jsondata.code=='0'){
								//this.form.image=jsondata.data["image"]
								uni.downloadFile({
								        url:jsondata.data["image"] , //仅为示例，并非真实的资源
								        success: function (res) {
								            if (res.statusCode === 200) {
												that.form.image=res.tempFilePath
								            }
								        }
								    });																
								this.form.title=jsondata.data["title"]
								this.form.text=jsondata.data["describe"]
								var tmplab=jsondata.data["lable"].split(",")
								var tmpadd=jsondata.data["address"].split(",")
								for(var i=0;i<tmplab.length-1;i++){
									this.form.attribute.push({
										text:tmplab[i]
										})
								}
								for(var i=0;i<tmpadd.length-1;i++){
									this.form.attribute2.push({
										text:tmpadd[i]
										})
								}													
				  			}else{
				  				 config.txtshow(jsondata.msg)				  				
				  			}	
														
				  	    },
				  		fail: () =>{  
				  			 config.txtshow('网络异常')
				  	     }, 
				  	});
				  }else{
				  	//跳转到登录界面
				  config.txtshow("未登录")	
				}						
			}
			
			
        }
    }
